.site-table {
  $root: &;

  width: 100%;
  color: #fff;
  border-collapse: collapse;
  text-align: left;

  th,
  td {
    padding: 10px;
  }

  &__head-row {
    th {
      color: #848ebd;
      font-size: 12px;
      border-bottom: 1px solid #848ebd;
    }
  }

  &__spacer-th {
    width: 100px;
  }

  &__row {
    &_expanded {
      color: #ff3399;
    }

    &_nested {
      color: #848ebd;

      td {
        padding-bottom: 25px;
      }
    }
  }

  &__btn {
    cursor: pointer;
    border: 1px solid #1eeff6;
    border-radius: 3px;
    color: #1eeff6;
    font-size: 12px;
    background: transparent;
    padding: 1px 14px;

    &:hover,
    &_expanded {
      border: 1px solid #ff3399;
      color: #ff3399;
    }
  }

  &__load-more-wrapper {
    text-align: center;
  }
}
