.site-footer {
  background: #0a0a0a;
  color: #fff;
  padding: 30px;

  &__inner {
    display: flex;
    justify-content: space-between;
    max-width: 1060px;
    margin: 0 auto;
  }

  &__docks-link {
    margin-bottom: 28px;
    color: #fff;
  }

  &__left {
    display: flex;
    flex-direction: column;
  }

  &__right {
    text-align: right;
  }

  &__wallet-title {
    font-size: 14px;
    margin-right: 8px;
  }

  &__wallet-address {
    margin-right: 8px;
    font-weight: 300;
  }
}
