.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #fff;
  padding-top: 90px;

  &__logo {
    width: 90px;
    height: 96px;
    background: url("./assets/logo.png") no-repeat center / contain;
    position: fixed;
    top: 5%;
    left: 4%;
  }

  &__inner {
    max-width: 1120px;
    width: 100%;
    margin: 0 auto;
    padding: 0 10px;
  }

  &__title {
    font-family: "Oswald", sans-serif;
    font-size: 28px;
    padding-left: 100px;
    margin: 0;
    margin-bottom: 45px;
  }

  &__text {
    line-height: 20px;
    max-width: 534px;
    padding-left: 100px;
    margin-bottom: 30px;
  }

  &__table {
    max-width: 1100px;
    margin: 0 auto;
    margin-bottom: 20px;
  }

  &__footer {
    margin-top: auto;
  }
}
